.intro-slogan {
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
  color: rgb(252, 252, 252);
  text-align: center;
  padding: 20px 0;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: 48px;
    padding: 0 45px;
    padding-bottom: 31px;
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
    line-height: 68px;
    padding: 0;
    padding-bottom: 50px;
  }
}
